.card {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 20px;
  border-bottom: 1px solid #2a363b;
}

.image {
  margin-left: 20px;
  margin-right: 30px;
  width: 300px;
  height: auto;
}

.title {
  font-size: 26px;
}

.subtitle {
  font-weight: 700;
  font-size: 20px;
}

.info {
  max-width: 800px;
}

.nav {
  text-align: center;
}

.link {
  display: inline-block;
  text-decoration: none;
  padding: 12px;
  font-weight: 500;
  color: #2a363b;
  font-size: 22px;
  font-weight: 700;
}

.activeLink {
  composes: link;
  color: #2196f3;
}

.list {
  padding-left: 10px;
  /* font-size: 16px; */
}

.item {
  padding-left: 0;
  /* font-size: 16px; */
}

.button {
  display: inline-block;
  width: 180px;
  height: 48px;
  border: 0;
  border-radius: 6px;

  background-color: rgb(219, 203, 203);
  background-size: 40%;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.6;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  outline: none;
}

.button:hover {
  opacity: 1;
}

.buttonLabel {
  font-size: 18px;
}
